import { settings } from './server';
import { getMaxDuration } from './times';

export const MAX = 9007199254740991;

let cachedPrice = {};

export function wordCount(text) {
  return text ? text.match(/\S+/g).length : 0;
}

export const getPriceDetailsForMST = (state, mst, words) => {
  const minutes = mst.asMinutes();

  if (cachedPrice[minutes]) {
    return cachedPrice[minutes];
  }
  let priceBreaks = [...settings(state).priceBreaks];
  let details;
  let previous;

  //Loop through price breaks until we find a MST break bigger than the current;
  for (let next of priceBreaks) {
    //Normalise down from infinity to something reasonable
    if (next.mst === MAX) next.mst = getMaxDuration(state, words).asMinutes();

    if (next.mst === minutes) {
      previous = Object.assign({}, next);
    } else if (next.mst > minutes) {
      details = Object.assign({}, next); //Take a copy of the pricebreak details so we can modify it
      if (previous) {
        // do some smoothing based on the difference between now and the previous
        const multiplier = (minutes - previous.mst) / (next.mst - previous.mst);
        details.uplift = previous.uplift - ((previous.uplift - next.uplift) * multiplier);
        details.margin = previous.margin - ((previous.margin - next.margin) * multiplier);
      }
      cachedPrice[minutes] = details;
      return details;
    } else {
      previous = Object.assign({}, next); //Do nothing, but remember this for later
    }
  }
  //We got to the end of the price breaks, most likely by just being over the new max
  cachedPrice[minutes] = previous;
  return previous;
};

export const round = (number) => {
  const factor = Math.pow(10, 2);
  const tempNumber = number * factor;
  const roundedTempNumber = Math.round(tempNumber);
  return roundedTempNumber / factor;
};

export const getPrice = (state, mst, words, time, maxTime) => {
  const pricing = getPriceDetailsForMST(state, mst, words);
  const pricePerWord = settings(state).basePrice + settings(state).basePrice * pricing.uplift;
  return round(Math.max(words * pricePerWord, getMinPrice(state, time, maxTime)));
};

export const getMinPrice = (state, time, maxTime) => (((maxTime - time) / maxTime) *
  (settings(state).minFastPrice - settings(state).minSlowPrice)) + settings(state).minSlowPrice;
