import React from 'react';
import styles from './TimeDisplay.module.scss';
import { locale } from '../constants';

const TimeDisplay = ({ time }) => {
  time.locale(locale);
  return (
    <dl className={styles.Container}>
      <dt>Livraison</dt>
      <dd>{time.calendar(null, {
        sameDay: (locale === 'fr') ? '[Aujourd\'hui a] LT' : '[Today at] LT',
        nextDay: (locale === 'fr') ? '[Demain a] LT' : '[Tomorrow at] LT',
        lastDay: (locale === 'fr') ? '[Hier a] LT' : '[Yesterday at] LT',
        nextWeek: (locale === 'fr') ? 'dddd [a] LT' : 'dddd [at] LT',
        sameElse: 'DD/MM/YYYY',
      })}</dd>
    </dl>
  );
};

export default TimeDisplay;
