import React from 'react';
import ReactDOM from 'react-dom';

import 'intl';
/* eslint-disable-line import/default */
import 'intl/locale-data/jsonp/fr';
import 'moment/locale/fr';
import fr from 'react-intl/locale-data/fr';
import 'babel-polyfill';
/* eslint-disable-line import/default */
import moment from 'moment';
import { addLocaleData, FormattedMessage, IntlProvider } from 'react-intl';
import lang from 'mybrian-common/lang';

import './index.css';
import './style/icomoon/style.css';
import './style/roboto/index.css';
import App from './App';
import * as serviceWorker from './serviceWorker';
import * as constants from './constants';
import { loadSettings } from './server';

addLocaleData([...fr]);
const locale = constants.locale;
const { format } = constants.localeFormatTweaks[locale] || undefined;
moment.locale(locale, format);

loadSettings().then(() => {
  ReactDOM.render(
    <IntlProvider locale={locale}
                  messages={lang.fr}>
      <App/>
    </IntlProvider>,
    document.getElementById('root'));
}).catch(() => {
  ReactDOM.render(<IntlProvider locale={locale}
                                messages={lang.fr}>
    <div style={{fontFamily: "Arial, sans-serif"}}>
      <FormattedMessage id="Error.Error.detail"/>
    </div>
    </IntlProvider>,
    document.getElementById('root'));
});
// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: http://bit.ly/CRA-PWA
serviceWorker.unregister();
