import { getPrice } from './pricing';
import {
  generateMarks,
  getEarliestTime,
  mustStartTime,
  timeFromIncrements,
} from './times';

export function calculatedValues(time, words = null) {
  const state = null;
  const { marks, max } = generateMarks(state, words);
  const start = getEarliestTime(state, words);
  const deadline = timeFromIncrements(state, time, start);
  const mst = mustStartTime(state, deadline, words);
  const price = getPrice(state, mst, words, time, max);
  return {
    words,
    marks,
    max,
    start,
    deadline,
    price,
  };
}
