import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import styles from './UploadField.module.scss';
import Dropzone from 'react-dropzone';
import { FormattedMessage } from 'react-intl';
import fileImg from '../style/img/upload-icon.png';

class UploadField extends React.Component {
  static propTypes = {
    accept: PropTypes.string,
    handleFile: PropTypes.func,
    handleError: PropTypes.func,
    uploading: PropTypes.bool,
    progress: PropTypes.number,
    className: PropTypes.string,
    disabled: PropTypes.bool,
    children: PropTypes.node,
  };

  static defaultProps = {
    accept: '',
    handleFile: () => {
    },
    handleError: () => {
    },
    uploading: false,
    progress: 0,
    disabled: false,
  };

  render() {
    const { accept, className, handleError, handleFile, uploading, progress, disabled } = this.props;
    return <Dropzone
      accept={accept}
      className={className}
      acceptClassName={styles.Accept}
      rejectClassName={styles.Reject}
      onDropRejected={handleError}
      onDropAccepted={handleFile}
      disabled={disabled}
    >
      {uploading ? <Fragment>
        <div className={styles.ProgressHolder}>
          <span className={styles.ProgressBar} style={{ width: progress + '%' }}/>
        </div>
        <div className={styles.Uploading}><FormattedMessage id="Order.Uploading"/></div>
      </Fragment> : <Fragment>
          <div className={styles.DragPrompt}><img src={fileImg} alt="fichier" /><p>Déposez votre<br /> fichier à traduire <br />ici</p></div>
        <p className={styles.Grow}>ou</p>
        <div className={styles.Button}><p>ajouter un fichier</p></div>
      </Fragment>}
    </Dropzone>;
  }
}

export default UploadField;
