import feathers from '@feathersjs/client';
import io from 'socket.io-client';

const socket = io();
let settingsCache = {};

// Configure feathers-client
const server = feathers().configure(feathers.socketio(socket, { timeout: 20000 }));
export const loadSettings = async () => {
  const serverSettings = await server.service('settings').find({
    query: {
      key: [
        'dayStart',
        'dayEnd',
        'minLength',
        'increments',
        'searchTime',
        'extendAfter',
        'extendBy',
        'searchTime',
        'bigJob',
        'basePrice',
        'priceBreaks',
        'minFastPrice',
        'minSlowPrice',
      ],
    },
  });
  if (serverSettings) {
    settingsCache = serverSettings.reduce((a, b) => {
      a[b.key] = b.value;
      return a;
    }, {});
    return settingsCache;
  }
};


export const settings = () => {
  return settingsCache;
};

export const getDayStart = () => settings().dayStart;
export const getDayEnd = () =>  settings().dayEnd;
export const getWeekend = () => false;

export default server;
