import React, { Component } from 'react';
import PropTypes from 'prop-types';

import Slider from 'rc-slider';
import styles from './TimePickerField.module.scss';
import './rc-slider.scss';

class TimePickerField extends Component {

  static propTypes = {
    input: PropTypes.object.isRequired,
    marks: PropTypes.object,
    max: PropTypes.number,
    meta: PropTypes.object,
    tooltip: PropTypes.object,
    onBeforeChange: PropTypes.func,
    onAfterChange: PropTypes.func,
  };

  render = () => {
    const { input, marks, max, onBeforeChange, onAfterChange } = this.props;

    if (!input.value) input.value = 0;
    return (
      <div className={styles.Container}>
        <div className="section-slider__slider">
          <Slider
            marks={marks}
            max={max}
            onBeforeChange={onBeforeChange}
            onAfterChange={onAfterChange}
            trackStyle={{backgroundColor: '#F6446B'}}
            railStyle={{backgroundColor: '#1A496B'}}
            {...input}
          />
        </div>
        <div className={styles.Tips}>
          <span className={styles.Fast}>Rapide</span>
          <span className={styles.Help}>Faites glisser le curseur</span>
          <span className={styles.Cheap}>Economique</span>
        </div>
      </div>
    );
  };

}

export default TimePickerField;
