import React, { Component } from 'react';
import { FormattedMessage, injectIntl, intlShape } from 'react-intl';
import request from 'superagent';

import styles from './App.module.scss';

import FileInfo from './display-elements/FileInfo';
import PriceDisplay from './display-elements/PriceDisplay';
import TimeDisplay from './display-elements/TimeDisplay';
import TimePickerField from './forms/TimePickerField';
import UploadField from './forms/UploadField';

import { allowedUploadTypes, locale } from './constants';
import { calculatedValues } from './utils';
import Button from './forms/Button';
import Step from './display-elements/Step';

class App extends Component {
  static propTypes = {
    intl: intlShape.isRequired,
  };

  state = {
    file: "87096a61765db56baf052e3c7204632fc0a41e797dc16db17dc7c56ee27e6294/monfichierdetraduction.docx",
    uploading: false,
    progress: 0,
    words: 200,
    error: null,
    time: 40,
  };
  handleError = () => {
    this.setState({ error: this.props.intl.formatMessage({ id: 'Order.MobileFile.UnacceptedType' }) });
  };

  handleFile = (acceptedFiles) => {
    if (acceptedFiles.length) {
      const file = acceptedFiles[0];
      this.setState({ uploading: true });

      request.post('/uploads')
             .attach('uri', file, file.name)
             .on('progress', event => {
               this.setState({ progress: event.percent });
             })
             .end((error, response) => {
               if (error) {
                 this.setState({
                   file: null,
                   error: error.message,
                   progress: 0,
                   uploading: false,
                 });
               } else {
                 this.setState({
                   error: null,
                   progress: 0,
                   words: response.body.words,
                   file: response.body.id,
                   uploading: false,
                 });
               }
             });
    }
  };

  className = () => {
    if (this.state.uploading) {
      return styles.UploadingContainer;
    }

    if (this.state.file) {
      return styles.FileContainer;
    }

    return styles.Dropzone;
  };

  handleClick = () => {
    document.cookie = `quotedOrderFile=${this.state.file}; domain=mybrian.fr; path=/;`;
    document.cookie = `quotedOrderTime=${this.state.time}; domain=mybrian.fr; path=/;`;
    window.location.href = 'https://client.mybrian.fr/order/new';
  };

  render() {
    const { marks, max, start, deadline, price } = calculatedValues(this.state.time, this.state.words);
    deadline.locale(locale);
    return (
      <div className={styles.App}>
        <div className={styles.Header}>
          <h3>Obtenez un devis en quelques secondes</h3>
        </div>
        <div className={styles.Step1}>
          <Step number={1} title="Chargez votre fichier" content="Formats acceptés :  .xlsx  .pptx .docx"/>
        </div>
        <div className={styles.Step2}>

          <Step number={2} title="Choisissez le prixr"
                content="Faites glisser le curseur pour ajuster votre ratio temps / prix idéal"/>
        </div>
        <div className={styles.Step3}>
          <Step number={3} title="Validez votre commande" content="Créez un compte et confirmez votre commande"/>
        </div>
        <div className={styles.FileContainer}>
          {this.state.error && <div className={styles.Error}>{this.state.error}</div>}
          <UploadField
            accept={Object.keys(allowedUploadTypes).toString()}
            className={styles.FileInfo}
            handleFile={this.handleFile}
            handleError={this.handleError}
            uploading={this.state.uploading}
            progress={this.state.progress}
          />
          {!!this.state.file && <FileInfo path={this.state.file} words={this.state.words}/>}
        </div>
        <div className={styles.Price}>
          <div className={styles.PriceTime}>
          <PriceDisplay price={price}/>
          <TimeDisplay time={deadline}/>
          </div>
          <TimePickerField
            marks={marks}
            max={max}
            start={start}
            meta={{}}
            input={{
              value: this.state.time,
              onChange: value => this.setState({ time: value }),
            }}
          />

        </div>


        <div className={styles.Continue}>
          <Button onClick={this.handleClick}>
            Commander ma traduction
          </Button>
        </div>


      </div>
    );
  }
}

export default injectIntl(App);
